import { Schema } from "form-render";

export interface RemoteComponent {
  config: Record<string, any>
  js: string
  css: string
  schema: Record<string, any>
  name: string
  version: string
}

export interface Component {
  id: string
  name: string
  props: Record<string, string | number | object>
  schema: Schema
  config?: RemoteComponent
}

export interface PageConfig {
  remoteComponents?: RemoteComponent[];
  userSelectComponents: any[]
  components: Component[]
  config: Record<string, any>
  page: any
}

export interface CurrentComponentSchema {
  schema: Schema
}

export interface CurrentComponent {
  component?: Component;
  currentComponentSchema?: CurrentComponentSchema;
  type?: string;
}

export interface EditConfig {
  componentConfig: Record<string, any>
  currentIndex: null | number | string
  currentComponent: CurrentComponent
}

export interface ReleaseStatus {
  test: [number, number, number]
  'pre-release': [number, number, number]
  master: [number, number, number]
}

export interface CommonComponents {
  groupName?: string // 远程组件分组名称
  components?: RemoteComponent[] // 远程组件列表
}

export interface UIConfig {
  commonComponents: CommonComponents[]
  showEdit: boolean
  releaseStatus: ReleaseStatus
  showRelease: boolean
  pageData: Record<string, any> // 页面数据
  dragStart: boolean
}

export interface EditState {
  currentIndex: number | null;
  pageConfig: PageConfig;
  editConfig: EditConfig
  uiConfig: UIConfig
  defaultConfig: any
  containerElementId: string
}

export const initialEditState: EditState = {
  currentIndex: null,
  pageConfig: {
    remoteComponents: [],
    userSelectComponents: [],
    components: [],
    config: {}, // 模板信息
    page: {}, // 页面样式&全局配置
  },

  editConfig: {
    componentConfig: {},
    currentIndex: null,
    currentComponent: {
      component: undefined,
      type: undefined,
      currentComponentSchema: { schema: {} }
    },
  },

  uiConfig: {
    commonComponents: [], // 远程组件列表
    showEdit: true,
    releaseStatus: {
      'test': [0, 0, 0],
      'pre-release': [0, 0, 0],
      'master': [0, 0, 0]
    },
    showRelease: false,
    pageData: {}, // 页面数据
    dragStart: false,
  },

  defaultConfig: null,
  containerElementId: '',
}

